a,
.link {
  text-decoration: none; }
  a--is-underlined,
  .link--is-underlined {
    text-decoration: underline; }

.navigation {
  height: 60px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: white;
  z-index: 100; }
  .navigation .nav-active span {
    color: #d2344a !important;
    font-weight: 700; }
  .navigation .logo {
    height: 35px;
    width: auto; }
  .navigation .nav-desktop {
    display: none;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%; }
    .navigation .nav-desktop .nav-center {
      display: flex; }
      .navigation .nav-desktop .nav-center .nav-parents-wrapper {
        padding: 0 24px;
        margin-left: 24px;
        position: relative; }
        .navigation .nav-desktop .nav-center .nav-parents-wrapper .nav-parents {
          display: flex;
          align-items: center;
          font-weight: 700;
          font-size: 14px;
          color: #15191e;
          cursor: pointer; }
          .navigation .nav-desktop .nav-center .nav-parents-wrapper .nav-parents .chevron {
            width: 24px;
            margin-left: 8px;
            transform: rotate(90deg); }
        .navigation .nav-desktop .nav-center .nav-parents-wrapper:hover .nav-child-wrapper {
          top: 27px;
          z-index: 1;
          opacity: 1;
          visibility: visible; }
        .navigation .nav-desktop .nav-center .nav-parents-wrapper .nav-child-wrapper {
          display: flex;
          visibility: hidden;
          flex-direction: column;
          position: absolute;
          top: 20px;
          z-index: -1;
          opacity: 0;
          left: 0;
          background-color: #fff;
          box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 25px 0px;
          border-radius: 5px;
          transition: all .3s ease; }
          .navigation .nav-desktop .nav-center .nav-parents-wrapper .nav-child-wrapper:after {
            content: '';
            width: 20px;
            height: 20px;
            background-color: white;
            position: absolute;
            top: -2px;
            left: 50%;
            transform: translateX(-50%) rotate(45deg);
            z-index: 0; }
          .navigation .nav-desktop .nav-center .nav-parents-wrapper .nav-child-wrapper .link-child-text {
            font-weight: 700;
            font-size: 14px;
            min-width: 140px;
            color: #15191e;
            margin: 0; }
          .navigation .nav-desktop .nav-center .nav-parents-wrapper .nav-child-wrapper .nav-child {
            padding: 12px 16px;
            position: relative;
            z-index: 1;
            transition: background-color .2s ease; }
            .navigation .nav-desktop .nav-center .nav-parents-wrapper .nav-child-wrapper .nav-child:hover {
              background-color: #e7e9ef; }
      .navigation .nav-desktop .nav-center .nav-child {
        display: flex;
        align-items: center;
        padding: 0 24px;
        margin-left: 24px; }
      .navigation .nav-desktop .nav-center .link-text {
        font-weight: 700;
        font-size: 14px;
        color: #15191e;
        transition: color .2s ease; }
        .navigation .nav-desktop .nav-center .link-text:hover {
          color: #d2344a; }
    .navigation .nav-desktop .nav-right button:first-child {
      margin-right: 24px; }
  .navigation .nav-mobile {
    padding: 0 16px;
    width: 100%;
    height: 100%;
    position: relative; }
    .navigation .nav-mobile .nav-mobile-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 100%; }
      .navigation .nav-mobile .nav-mobile-top .hamburger {
        width: 24px; }
    .navigation .nav-mobile .menu {
      display: none;
      background: white;
      width: 100%;
      position: absolute;
      padding: 0 16px;
      top: 60px;
      left: 0;
      overflow-y: scroll;
      max-height: calc(100vh - 60px); }
      .navigation .nav-mobile .menu .nav-parents-wrapper .nav-parents {
        font-size: 18px;
        color: #0a0a0a;
        font-weight: 700;
        margin: 16px 0; }
      .navigation .nav-mobile .menu .nav-parents-wrapper .nav-child-wrapper .nav-child-item {
        font-size: 14px;
        padding: 12px 0px;
        display: block;
        color: #5F6D87;
        width: 100%;
        border-bottom: 1px solid #eaeaea; }
      .navigation .nav-mobile .menu .nav-item {
        padding: 12px 0px;
        display: block;
        font-size: 16px;
        color: #15191e;
        font-weight: 700; }
      .navigation .nav-mobile .menu-active {
        display: block;
        border-bottom: 1px solid #D2D7DF; }
  @media only screen and (min-width: 992px) {
    .navigation {
      height: 80px; }
      .navigation .nav-desktop {
        display: flex; }
      .navigation .nav-mobile {
        display: none; } }
