a,
.link {
  text-decoration: none; }
  a--is-underlined,
  .link--is-underlined {
    text-decoration: underline; }

.footer {
  background-color: #0a0a0a; }
  .footer h5 {
    font-size: 16px;
    color: white;
    font-weight: 700; }
  .footer a {
    text-decoration: none;
    font-size: 14px;
    display: block;
    margin-bottom: 8px;
    width: 65px;
    color: #D2D7DF; }
  .footer p {
    line-height: 1.75;
    font-size: 14px;
    color: #D2D7DF; }
  .footer .social-wrapper {
    display: flex;
    margin-top: 24px; }
    .footer .social-wrapper a {
      width: initial;
      margin-right: 40px; }
      .footer .social-wrapper a img {
        width: 24px; }
  .footer form {
    display: flex;
    flex-direction: column; }
    .footer form input, .footer form textarea {
      background-color: white;
      margin-bottom: 16px; }
  .footer .app-download {
    display: flex;
    margin-top: 36px; }
    .footer .app-download a {
      width: initial;
      margin-right: 16px; }
      .footer .app-download a img {
        width: 135px; }
    .footer .app-download a:last-child img {
      width: 151px; }
  .footer .footer-bottom {
    padding: 24px 0;
    width: 100%;
    background-color: #333333;
    font-size: 14px; }
    .footer .footer-bottom .left {
      color: white; }
    .footer .footer-bottom .right {
      display: flex;
      flex-direction: column; }
    .footer .footer-bottom a {
      display: block;
      width: initial;
      margin-bottom: 8px; }
    @media only screen and (min-width: 768px) {
      .footer .footer-bottom .right {
        align-items: center;
        justify-content: space-between;
        flex-direction: row; }
      .footer .footer-bottom a {
        margin-bottom: 0px; } }
